import React from "react";
import { format, subDays } from "date-fns";

// Color logic based on submission count
const getColor = (count: number): string => {
    if (count === 0) return "#ebedf0";
    if (count < 3) return "#9be9a8";
    if (count < 6) return "#40c463";
    if (count < 10) return "#30a14e";
    return "#216e39";
  };
// Generate sample submission data
const generateChartData = () => {
  const days = 180;
  const today = new Date();
  const data = [];
  for (let i = 0; i < days; i++) {
    const date = subDays(today, i);
    data.unshift({
      date: format(date, "yyyy-MM-dd"),
      count: Math.floor(Math.random() * 12), // Replace with actual data if needed
    });
  }
  return data;
};

// React component renamed to "chart"
const chart = () => {
  const data = generateChartData();
  const weeks = [];

  for (let i = 0; i < data.length; i += 7) {
    weeks.push(data.slice(i, i + 7));
  }

  const totalSubmissions = data.reduce((sum, d) => sum + d.count, 0);

  return (
    <div className="col-span-6 p-4 bg-[#f5f6f8] rounded-xl w-full md:w-fit">
    <h2 className="text-gray-800 font-semibold mb-2">
      Leetcode - {totalSubmissions} Submissions in past 6 months
    </h2>
    <div className="flex gap-[2px]">
      {weeks.map((week, i) => (
        <div key={i} className="flex flex-col gap-[2px]">
          {week.map((day, j) => (
            <div
              key={j}
              title={`${day.count} submissions on ${day.date}`}
              className="w-3 h-3 rounded-sm"
              style={{ backgroundColor: getColor(day.count) }}
            ></div>
          ))}
        </div>
      ))}
    </div>
  </div>
  
  );
};

export default chart;
